<template>
  <div class="home">
    <!-- 导航栏 -->
    <van-tabs
      v-model="active"
      animated
      line-height="2px"
      title-active-color="#ee0a24"
      @change="toggleNav"
      sticky
    >
      <van-tab v-for="(nav,index) in navData" :title="nav.title" :key="index"></van-tab>
    </van-tabs>

    <!-- 二级路由 -->
    <router-view @current-song="$emit('current-song', $event)"></router-view>
  </div>
</template>

<script>
// import Hot from "@/components/Hot.vue";

export default {
  name: "Home",
  components: {},
  data() {
    return {
      active: 0,
      current: 0,
      navData: [
        { title: "推荐音乐", routerUrl: "Recommend" },
        { title: "热歌榜", routerUrl: "Hot" },
        { title: "搜索", routerUrl: "Search" }
      ]
    };
  },
  methods: {
    toggleNav(n) {
      var name = this.navData[n].routerUrl;
      this.$router.push({ name });
    }
  }
};
</script>
